import axios from 'axios';
import qs from 'qs';
import {Loading} from 'element-ui';


const service = axios.create({
    // process.env.NODE_ENV === 'development' 
    // easy-mock
    // baseURL: 'https://www.easy-mock.com/mock/592501a391470c0ac1fab128',
    timeout: 5000000,
    // `transformResponse`  then/catch 
});

let loadingInstance;
let loadingRequestCount = 0;


const showLoading = (config) => {
    if (config.showLoading == false){return }
    if (loadingRequestCount === 0) {
        loadingInstance = Loading.service({
            body: true,
            fullscreen: true,
            text: "..."
        });
    }
    loadingRequestCount++
}

const hideLoading = (config) => {
    // this.hsConsoleInfo("loading")
    if(config.showLoading == false){return;}
    if (loadingRequestCount <= 0) return
    loadingRequestCount--
    if (loadingRequestCount === 0) {
        HSUtil.getDefVue().$nextTick(() => {// Loading 
            loadingInstance.close();
        });
    }
}

// axios.defaults.headers.post['Content-Type'] = 'application/json';
axios.defaults.headers.post['Content-Type'] = 'multipart/form-data';

service.interceptors.request.use(
    config => {
        showLoading(config);
        config.headers = {'content-type': 'application/x-www-form-urlencoded'};
        config.data = qs.stringify(config.data);
        config.headers.token = sessionStorage.getItem('ms_token');
        return config;
    },
    error => {
        HSUtil.getDefVue.hsConsoleError("requestError:"+error);
        return Promise.reject();
    }
);


service.interceptors.response.use(
    response => {
        // setTimeout  
        setTimeout(() => {
            hideLoading(response.config)
        }, 200);
        if (response.status === 200) {
            return response.data;
        } else {
            Promise.reject();
        }
    },
    error => {
        HSUtil.hsConsoleInfo("requestError:"+error);
        if (error == "Error: Request failed with status code 500"){
            HSUtil.hs_msg_error("500");
        }
        if (error == "Error: Request failed with status code 404"){
            HSUtil.hs_msg_error("404");
        }
        setTimeout(() => {
            hideLoading(error.response.config);
        }, 200);
        return Promise.reject();
    }
);

export default service;

